@import url(https://fonts.googleapis.com/css2?family=Roboto:ital,wght@0,100;1,300&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Damion&display=swap);
@import url(https://fonts.googleapis.com/css2?family=EB+Garamond&display=swap);
.App {
  background-color: #ffffff;
  background-image: linear-gradient(62deg, #ffffff 0%, #dfd4e4 100%);
  
}
* {
  font-family: 'Roboto', sans-serif;
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

.edu {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
}
.awards {
  display: flex;
}

.head{
  color: #4caf50;
  display: grid;
  justify-items: center;
  align-items: center;
}
.head:hover{
  cursor: pointer;
}
.head > h1 {
  display: flex;
  align-items: center;
}

.head > h1{
  font-weight: lighter;
  font-size: 50px;
  font-family: 'Damion', cursive;
}
.input-wrapper{
  width: 700px;
  height: 900px;
  margin-left: auto;
  margin-right: auto;
}
.input-wrapper > h1{
  font-family: 'Damion', cursive;
  font-weight: lighter;
  font-size: 35px;
  color: #4caf50;
  display: flex;
  flex-direction: row;
}
.header:before, .header:after {
  content: "";
  flex: 1 1;
  border-bottom: 1px solid #000;
  margin: auto;
}
.header:before {
  margin-right: 10px;
}

.header:after {
  margin-left: 10px;
}

input{
  border: none;
  outline: none;
  border-radius: 10px;
  height: 50px;
  font-weight: bold;
  margin: 20px;
}
input:focus{
  background-color: beige;
  transition: 0.4s;
}

button{
  background:none;
  border: none;
  outline: none;
  cursor: pointer;
}
button:hover{
transform: scale(1.1);
}
.add-work{
  display: block;
  margin-left: auto;
  margin-right: auto;
  
}

.skills-info > div{
  display: flex;
  place-content: center;
  margin-bottom: -15px;
}

.work-experience > div {
  border-bottom: 0.5px dotted black;
}
.work-button-wrapper{
  display: flex;
}

.scroll-cv{
  margin-bottom: 10px;
  margin-left: auto;
  margin-right: auto;
  width: 20%;
}
.scroll-button{
  color: red;
  font-size: 20px;
  font-weight: bolder;
}
li{
  font-weight: bolder;
}
.container{
  margin-top: 10px;
  margin-left: auto;
  margin-right:auto;
  height: 1100px;
  width: 850px;
  background-color: #f5f5f5;
  display: grid;
  place-items: center;
  
}
.wrapper{
  height: 908px;
  width: 658px;
  
}

.general{
  border-bottom: 3px solid black;
}
.general > h1{
  margin: 0;
  font-family: 'EB Garamond', serif;
  font-size: 32px;
}
.general > ul{
  font-size: 22px;
  margin: 0;
  padding: 0;
  font-family: 'EB Garamond', serif;
  display: flex;
  align-items: flex-start;
  justify-content: space-between;
  list-style-type: none;
  font-weight: bold;
}

.work-exp {
  border-bottom: 2px solid black;
  min-height: 200px;
  margin-bottom: 5px;
}
.work-exp > h2{
  width: 658px;
  border-bottom: 2px solid black;
  margin-top: 3px;
  margin-bottom: 5px;
  font-weight: bolder;

}
.work-info {
  display: grid;
  margin-top: 10px;
  grid-template-columns: 5fr 2fr;
  font-family: 'EB Garamond', serif;
}
.work-info> h1{
  font-size: 24px;
 
}
.work-info > h4{
  font-size: 15px;
  font-style: italic;
}
.education {
  border-bottom: 2px solid black;
  margin-bottom: 10px;
}
.education > h2 {
  border-bottom: 2px solid black;
  margin-bottom: 5px;
}
.edu-info {
  display: grid;
  grid-template-columns: 5fr 2fr;
  font-family: 'EB Garamond', serif;
}
.edu-info > h2 {
 font-size: 1.5em;
}
.edu-info > h3 {
  font-style: italic;
  
 }
.edu-info > h4 {
  font-style: italic;
  font-size: 15px;
}

.skills-info{
  width: 658px;
  min-height: 100px;
 
}
.skills-info>ul{
  list-style-type: none;
  width: 658px;
}
.skills-info>ul>li{
  margin-top: 5px;
  margin-right: 5px;
}
.skills > h2 {
  border-bottom: 2px solid black;
}
